import React, { Component } from 'react';
import styles from './scss/Right_list.module.scss';

class Right_list extends Component {
  state = {
    doing: false
  };

  handleDoItem = item => {
    this.props.handleDoItem(item);
    this.setState({ doing: item });
  };

  render() {
    const { itemList } = this.props;

    return (
      <div className={styles.Right_list}>
        <ul className={styles.list_wrap}>
          {itemList.map((item, idx) => {
            return (
              <li
                onClick={() => {
                  this.handleDoItem(item.name);
                }}
                className={`${styles.list_item} ${this.state.doing ===
                  item.name && styles.on}`}
                key={idx}
              >
                <span>{item.name}</span>
                <i className="material-icons">play_circle_outline</i>
              </li>
            );
          })}
        </ul>
        <div className={styles.more}>
          <a>MORE</a>
        </div>
      </div>
    );
  }
}

export default Right_list;
