import React, { Component } from 'react';
import cl from './scss/Right_main.module.scss';

class Right_main extends Component {
  render() {
    const { doItem, min, sec } = this.props;
    return (
      <div className={cl.main}>
        <div className={cl.title}>
          <div>{doItem}</div>
          <div className={cl.circle}>
            <span />
          </div>
        </div>
        <div className={cl.time}>
          {min}:{sec}
        </div>
      </div>
    );
  }
}

export default Right_main;
