import React, { Component } from 'react';
import styles from './scss/Player.module.scss';

let timer, canvas, ctx, centerX, centerY;
let min = 25;
let sec = 0;
let totalSec = min * 60;

const radius = 527 / 2;
const startPi = Math.PI * 1.5;

class Player extends Component {
  constructor(props) {
    super(props);
    this.canvas = React.createRef();
  }

  componentDidMount() {
    canvas = this.canvas.current;
    ctx = canvas.getContext('2d');
    centerX = canvas.width / 2;
    centerY = canvas.height / 2;
  }

  componentDidUpdate() {
    if (!this.props.isPlay) {
      clearInterval(timer);
      ctx.clearRect(0, 0, canvas.width, canvas.height);
    }
  }

  drawCircle = (endPi, width, color, clock) => {
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, startPi, endPi, clock); // true: 逆時針, flase: 順時針
    ctx.lineWidth = width;
    ctx.strokeStyle = color;
    ctx.stroke();
  };

  handlePlay = () => {
    clearInterval(timer);
    if (!this.props.isPlay) {
      this.startTimer();
      this.props.handlePlay(true);
    } else {
      this.props.handlePlay(false);
    }
  };

  startTimer = () => {
    timer = setInterval(() => {
      if (totalSec === sec) {
        clearInterval(timer);
        return;
      }

      sec++;
      let circle = ((Math.PI * 2) / totalSec) * sec + startPi;

      this.drawCircle(circle, 19, '#ff4384', false);
      this.props.getTime(sec);
    }, 10);
  };

  render() {
    const { isPlay } = this.props;

    return (
      <div className={`${styles.player} ${!!isPlay && styles.on}`}>
        <div className={styles.player_wrap}>
          <canvas ref={this.canvas} width="540" height="540" />
          <div className={styles.play_btn}>
            <i className="material-icons" onClick={this.handlePlay}>
              {!isPlay ? 'play_circle_filled' : 'pause_circle_filled'}
            </i>
          </div>
        </div>
      </div>
    );
  }
}

export default Player;
