import React, { Component } from 'react';

import './css/reset.css';
import './css/common.css';
import styles from './scss/App.module.scss';

import Right_add from './Right_add.js';
import Right_main from './Right_main.js';
import Right_list from './Right_list.js';
import Player from './Player.js';
import Menu from './Menu.js';

class App extends Component {
  state = {
    newItem: '',
    doItem: '',
    itemList: [
      {
        name: 'THE FIRST THING TO DO TODAY',
        done: false
      }
    ],
    min: 25,
    sec: '00',
    isPlay: false
  };

  componentDidMount() {
    const itemList = this.state.itemList;
    this.setState({
      doItem: itemList.length > 0 ? itemList[0].name : 'NOTHING TO DO TODAY'
    });
  }

  handleChangeItem = item => {
    let newList = this.state.itemList.slice();

    newList.push({
      name: item,
      done: false
    });

    this.setState({
      newItem: item,
      itemList: newList
    });
  };

  handleDoItem = item => {
    if (this.state.doItem === item) return;

    let option = {
      doItem: item,
      min: 25,
      sec: '00',
      isPlay: false
    };

    this.setState(option, () => {
      console.log(item);
    });
  };

  getTime = time => {
    let currentSec = 60 - (time % 60);
    if (currentSec === 60) currentSec = 0;
    currentSec = timerRange(currentSec);

    let currentMin = 25 - Math.floor(time / 60) - 1;
    if (currentMin < 0) currentMin = 0;
    currentMin = timerRange(currentMin);

    this.setState({
      sec: currentSec,
      min: currentMin
    });

    function timerRange(time) {
      if (time === 0) {
        return '00';
      } else if (time > 0 && time < 10) {
        return '0' + time;
      } else {
        return time;
      }
    }
  };

  handlePlay = play => {
    this.setState({ isPlay: play });
  };

  render() {
    const { doItem, itemList, min, sec, isPlay } = this.state;

    return (
      <div className={styles.app}>
        <div className={styles.right}>
          <div className={styles.right_wrap}>
            <Right_add getItem={item => this.handleChangeItem(item)} />

            <Right_main doItem={doItem} min={min} sec={sec} />

            <Right_list
              itemList={itemList}
              handleDoItem={item => this.handleDoItem(item)}
            />

            <Player
              getTime={sec => this.getTime(sec)}
              isPlay={isPlay}
              handlePlay={play => this.handlePlay(play)}
            />
          </div>
        </div>
        <div className={styles.left}>
          <ul>
            <li>
              <i class="material-icons">list</i>
            </li>
            <li>
              <i class="material-icons">assessment</i>
            </li>
            <li>
              <i class="material-icons">library_music</i>
            </li>
          </ul>
          <span>POMODORO</span>
        </div>
        <Menu />
      </div>
    );
  }
}

export default App;
