import React, { Component } from 'react';
import cl from './scss/Right_add.module.scss';

class Right_add extends Component {
  constructor(props) {
    super(props);
    this.newItem = React.createRef();
  }

  handleChange = () => {
    this.props.getItem(this.newItem.current.value);
    this.newItem.current.value = '';
  };

  render() {
    return (
      <div className={cl.right_add}>
        <input
          type="text"
          placeholder="ADD A NEW MISSION"
          ref={this.newItem}
          onKeyPress={e => {
            if (e.key === 'Enter') this.handleChange();
          }}
        />
        <span className={cl.add_new} onClick={() => this.handleChange()} />
      </div>
    );
  }
}

export default Right_add;
